"use strict";

// all func
// ready func
(function () {
  var header = document.querySelector('.header');

  function toggleMenu() {
    header.classList.toggle("open-menu");
  }

  header.onclick = function (event) {
    var burger = event.target.closest('.js-burger');

    if (burger) {
      toggleMenu();
    }
  };
})(); // ready func end